a{
  text-decoration: none;
  color: black;
}


.body {
  align-items: center;
  margin: 30px auto;
  width: 80%;
  background: rgb(253, 253, 253);
  justify-content: center;
  border: none;
  border-radius: 10px;
  min-height: 1000px;
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.container {
  justify-content: center;

  .title {
    font-size: 1.8rem;
    font-weight: 600;
    margin-bottom: 30px;
    margin-left: 50px;
  }
}
